<script>
    import {term, category, showResults, list} from './stores.js'
    import IoIosBasket from 'svelte-icons/io/IoIosBasket.svelte'
    import { TabContent, TabPane, Offcanvas } from 'sveltestrap';
    
    
  let endOpen = false;
  const toggleEnd = () => {
    endOpen = !endOpen
};
    


    // let isOpen = false
    let val = ''
    let selected
    $:category.set(selected) 

    // $:term.set(val) //MAKE IT LIVE RESPONSE ON INPUT


    ////ON MOUSE DOWN IN SEARCH
    const onKeyPress = e => {
    if (e.charCode === 13) {
        term.set(val.toUpperCase())
        selected = ''
        showResults.set(true)
        e.target.blur()
    };
  };



  //////Getting totals
  let status
 let sum_energy, sum_carbohydrate, sum_fiber, sum_lipid, sum_protein


let getTotals = ()=>{
let arr =  $list.find((el) => el.name == status)          
   
 sum_energy = arr.foods.reduce(function (total, currentValue) {
    return parseInt(total) + parseInt(currentValue.energy_kcal);
}, 0)
sum_protein = arr.foods.reduce(function (total, currentValue) {
    return parseInt(total) + parseInt(currentValue.protein_g);
}, 0)
sum_lipid = arr.foods.reduce(function (total, currentValue) {
    return parseInt(total) + parseInt(currentValue.lipid_total_g);
}, 0)
sum_carbohydrate = arr.foods.reduce(function (total, currentValue) {
    return parseInt(total) + parseInt(currentValue.carbohydrate_g);
}, 0)
sum_fiber = arr.foods.reduce(function (total, currentValue) {
    return parseInt(total) + parseInt(currentValue.fiber_g);
}, 0)

    

}












</script>
      



  <div class="container-fluid">
    <div class="row">
        <div class="col-6">
            <input class="form form-control" 
            style="text-transform: uppercase;"
            placeholder="Search food"
            on:keypress={onKeyPress}
            type="text" bind:value="{val}">
            </div>
    
         
    
          <div class="col-5">
            <select name="" class="from form-control" id=""
            bind:value={selected}
            >
                <option value="" disabled selected>Filter</option>
                <option value="">Any</option>
                <option value="Boiled">Boiled</option>
                <option value="Roasted">Roasted</option>
                <option value="Steamed">Steamed</option>
                <option value="Raw">Raw</option>
                <option value="Fried">Fried</option>
            </select>
          </div>
    

          <div class="col-1">
              <div class="{$list.length > 0 ? 'iconyes' : 'icon'}" style="cursor: pointer;"
              on:click={() => {
                //   getTotals()
                //   endOpen = !endOpen
                  }}
              >
                    <IoIosBasket></IoIosBasket>
              </div>
          </div>


    </div>
  </div>

      

<!-- OFFCANVAS -->
<Offcanvas isOpen={endOpen} toggle={toggleEnd} placement="end" header="View your lists" style="width: 100vw">
    
    <div class="container-fluid mx-0">

        
        <div class="row">
            <TabContent on:tab={(e) => {
                status = e.detail
                getTotals()
            }}>
                {#each $list as l}
                <TabPane tabId="{l.name}" tab="{l.name}" active>

                    <div class="container mt-2">
                        <div class="row">
                            <div class="col mx-auto">
                                <div class="card text-center p-1 shadow-sm mx-auto">
                        <table class="table table-borderless table-sm custom">
                            <tbody>
                                <tr>
                                    <td colspan="2">
                                        <h5 class="text-center">TOTALS</h5>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        Energy
                                    </td>
                                    <td>
                                        {sum_energy} KCal
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        Carbohydrates
                                    </td>
                                    <td>
                                        {sum_carbohydrate} g
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        Protein
                                    </td>
                                    <td>
                                        {sum_protein} g
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        Lipids
                                    </td>
                                    <td>
                                        {sum_lipid} g
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                    Fiber    
                                    </td>
                                    <td>
                                        {sum_fiber} g
                                    </td>
                                </tr>
                            </tbody>
                        </table>

                      
                                </div>
                            </div>
                        </div>
                    </div>

                   <div class=" row mt-2">

                    <table class="table table-responsive">

                        <thead class="text-white bg-success">
                            <tr>
                            <td>Food</td>
                            <td>per quantity</td>
                            <td>State</td>
                            <td>Energy (Kcal)</td>
                            <td>Protein (g)</td>
                            <td>Total Lipid (g)</td>
                            <td>Carbohydrate (g)</td>
                            <td>Fiber (g)</td>
                            <td>Water (g)</td>
                         </tr>
                        
                        </thead>


                        <tbody>
                    {#each l.foods as f}
                       <tr>
                           <td>{f.food_description}</td>
                           <td>{f.quantity} g/ml</td>
                            <td>{f.food_state}</td>
                             <td>{f.energy_kcal}</td>
                             <td>{f.protein_g}</td>
                             <td>{f.lipid_total_g}</td>
                              <td>{f.carbohydrate_g}</td>
                      <td>{f.fiber_g}</td>
                         <td>{f.water_g}</td>
                    
                    </tr>
                    {/each}
                        </tbody>
                    </table>
                   </div>
                    

                  </TabPane>
                {/each}
               
              </TabContent>
        </div>
    </div>
    
</Offcanvas>




















<style>
  .icon {
    color: grey;
    opacity: 0.6;
    width: 32px;
    height: 32px;
  }
  .iconyes{
    color: black;
    width: 32px;
    height: 32px;
  }

  

</style>