<script>
import { filtered, list } from "./stores.js";
import {Modal, ModalBody, ModalFooter, ModalHeader,
    Dropdown,
    DropdownItem,
    DropdownMenu,
    DropdownToggle
} from 'sveltestrap'

import { toast } from '@zerodevx/svelte-toast'

let selectedfood = []
let listhere = []

//////QUANTITY
let quantity = 100


///Results 
$: factor = quantity / 100


///////TEST MODAL
let Mopen = false;
const toggleM = () => {
    Mopen = !Mopen
    quantity = 100 
};
let size = 'xl';
let fullscreen = 'xl'












///On enter quantity
 ////ON MOUSE DOWN IN SEARCH
 const onKeyPress = e => {
    if (e.charCode === 13) {
        e.preventDefault(); 
    };
  };


//////Add to list
let listName = ''




























</script>

<div class="container">
   

<div class="row">
        {#each $filtered as item}
    <div class="col-sm-12 col-md-8 col-lg-6 col-xl-5" 
    on:click|preventDefault="{()=>{
        selectedfood = item
        Mopen = true
    }}">


            <div class="card shadow-sm my-2" style="cursor: pointer;">
                <div class="card-body py-1">
                    <p class="mb-0">{item.food_description}</p>
                    <span class="{
                    item.food_state=='Fried' ? 'bg-primary text-white'
                :   item.food_state=='Boiled' ? 'bg-success text-white'
                :   item.food_state=='Raw' ? 'bg-danger text-white'
                :   item.food_state=='Roasted' ? 'bg-warning'
                :   item.food_state=='Steamed' ? 'bg-info text-white'
                : 'text-white'
            } px-2 " style="border-radius: 3px;">
                        <span class="small">{item.food_state}</span>
                        </span>
</div>
            </div>

        
    </div>
        {/each}
   </div>
    



   <Modal isOpen={Mopen} {toggleM} {size} {fullscreen}>
    <ModalHeader {toggleM}>
        
        {selectedfood.food_description}
    
      
    
    </ModalHeader>
    <ModalBody>
    
    <div class="d-flex flex-row-reverse">
        <p class="right">
   
            <Dropdown>
                <DropdownToggle caret>Add to list</DropdownToggle>
                <DropdownMenu dark>
                 {#each $list as l}
                 <DropdownItem disabled on:click={()=>{
                    listhere.find(el => el.name == l.name).foods.push(
                    
                        {
                        quantity: quantity,
                        food_description: selectedfood.food_description,
                        energy_kcal: (selectedfood.energy_kcal * factor ).toFixed(0),
                        carbohydrate_g: (selectedfood.carbohydrate_g * factor ).toFixed(0),
                        protein_g: (selectedfood.protein_g * factor ).toFixed(0),
                        lipid_total_g: (selectedfood.lipid_total_g * factor ).toFixed(0),
                        fiber_g: (selectedfood.fiber_g * factor ).toFixed(0),
                        food_state: selectedfood.food_state ,
                        water_g: (selectedfood.water_g * factor ).toFixed(0),
                        fct_source: selectedfood.fct_source ,
                        iron_mg: (selectedfood.iron_mg * factor ).toFixed(0),
        
                    }
                    
                    )
        
                    toast.push(`<p>${selectedfood.food_description} has been added to ${l.name}</p>`, {
          theme: {
            '--toastBackground': '#48BB78',
            '--toastBarBackground': '#2F855A'
          }
        })
        
        list.set(listhere)
        
        
        toggleM()
                }}><span>{l.name}</span></DropdownItem>
                 {/each}
        
        <DropdownItem disabled on:click={()=>{
             listName = prompt("Please name your list", "list_"+ ('00000' + Math.random().toString(36).substr(2, 5)).slice(-5))
            if(listName === null){
                return
            }
        
        
            if(listName){
               listhere = [...listhere, {
                name: listName,
                foods: [
                    {
                        quantity: quantity,
                        food_description: selectedfood.food_description,
                        energy_kcal: (selectedfood.energy_kcal * factor ).toFixed(0),
                        carbohydrate_g: (selectedfood.carbohydrate_g * factor ).toFixed(0),
                        protein_g: (selectedfood.protein_g * factor ).toFixed(0),
                        lipid_total_g: (selectedfood.lipid_total_g * factor ).toFixed(0),
                        fiber_g: (selectedfood.fiber_g * factor ).toFixed(0),
                        food_state: selectedfood.food_state ,
                        water_g: (selectedfood.water_g * factor ).toFixed(0),
                        fct_source: selectedfood.fct_source ,
                        iron_mg: (selectedfood.iron_mg * factor ).toFixed(0),
                    }
                ]
            }]
            }
        
            toast.push(`<p>${selectedfood.food_description} has been added to ${listName}</p>`, {
          theme: {
            '--toastBackground': '#48BB78',
            '--toastBarBackground': '#2F855A'
          }
        })
        
        list.set(listhere)
        
        
            toggleM()
        
        }}><span>Feature yet to be added</span></DropdownItem>
                  
                </DropdownMenu>
              </Dropdown>
            
         </p>
        
    </div> 
        
 
        <div class="container mt-4">


            <p class="lead text-center"><small>per 
                <span contenteditable="true" on:keypress={onKeyPress} bind:innerHTML="{quantity}">{quantity}</span> 
                g(/ml)</small></p>
            <input type="range" class="form form-control form-range my-3" bind:value="{quantity}" min="1" max="500">
     
     
     
     <div class="row ">
     <div class="col-12">
         <div class="bg-primary text-white">
             <h3 class="py-0 text-center lead">Energy</h3>
             <h3 class="py-0 text-center lead">{(selectedfood.energy_kcal * factor ).toFixed(0)} Kcal</h3>
            </div>
     </div>
     </div>
     
     <div class="row mx-2">
     
         <div class="col-4 px-0">
             <div class="bg-success text-white">
                 <p class="py-0 mb-0 lead text-center">Protein</p>
                 <p class="pb-1 text-center lead">{(selectedfood.protein_g * factor ).toFixed(0)} g</p>
                </div>
         </div>
     
         <div class="col-4 px-0">
             <div class="text-white" style="background-color: #AE8E1C;">
                 <p class="py-0 mb-0 lead text-center">Lipids</p>
                 <p class="pb-1 text-center lead">{(selectedfood.lipid_total_g * factor ).toFixed(0)} g</p>
                </div>
         </div>
     
         <div class="col-4 px-0">
             <div class="bg-secondary text-white">
                 <p class="py-0 mb-0 lead text-center text-wrap">Carbs</p>
                 <p class="pb-1 text-center lead">{(selectedfood.carbohydrate_g * factor ).toFixed(0)} g</p>
                </div>
         </div>
     
         <div class="col-4 px-0">
             <div class="text-white" style="background-color: #9B3D12">
                 <p class="py-0 mb-0 lead text-center">Fiber</p>
                 <p class="pb-1 text-center lead">{(selectedfood.fiber_g* factor ).toFixed(0)} g</p>
                </div>
         </div>
         <div class="col-4 px-0">
             <div class="bg-light text-dark shadow-sm">
                 <p class="py-0 mb-0 lead text-center">Calcium</p>
                 <p class="pb-1 text-center lead">{(selectedfood.calcium_mg* factor ).toFixed(0)} mg</p>
                </div>
         </div>
         <div class="col-4 px-0">
             <div class=" text-white" style="background-color: #963483;">
                 <p class="py-0 mb-0 lead text-center">Iron</p>
                 <p class="pb-1 text-center lead">{(selectedfood.iron_mg* factor ).toFixed(0)} mg</p>
                </div>
         </div>
         <div class="col-4 px-0">
             <div class=" text-white" style="background-color:#3066BE;">
                 <p class="py-0 mb-0 lead text-center">Zinc</p>
                 <p class="pb-1 text-center lead">{(selectedfood.zinc_mg* factor ).toFixed(0)} mg</p>
                </div>
         </div>
         <div class="col-4 px-0">
             <div class=" text-white" style="background-color:#28C2FF;">
                 <p class="py-0 mb-0 lead text-center">Vitamin C</p>
                 <p class="pb-1 text-center lead">{(selectedfood.vit_c_mg* factor ).toFixed(0)} mg</p>
                </div>
         </div>
         <div class="col-4 px-0">
             <div class=" text-white" style="background-color: #BA274A;">
                 <p class="py-0 mb-0 lead text-center">Thiamin</p>
                 <p class="pb-1 text-center lead">{(selectedfood.thiamin_mg* factor ).toFixed(0)} mg</p>
                </div>
         </div>
         <div class="col-4 px-0">
             <div class=" text-white" style="background-color: #BA5C12">
                 <p class="py-0 mb-0 lead text-center">Riboflavin</p>
                 <p class="pb-1 text-center lead">{(selectedfood.riboflavin_mg* factor ).toFixed(0)} mg</p>
                </div>
         </div>
         <div class="col-4 px-0">
             <div class=" text-white" style="background-color: #3E2F5B">
                 <p class="py-0 mb-0 lead text-center">Niacin</p>
                 <p class="pb-1 text-center lead">{(selectedfood.niacin_mg* factor ).toFixed(0)} mg</p>
                </div>
         </div>
         <div class="col-4 px-0">
             <div class=" text-white" style="background-color: #3A4454">
                 <p class="py-0 mb-0 lead text-center">Vitamin C</p>
                 <p class="pb-1 text-center lead">{(selectedfood.vit_b_mg* factor ).toFixed(0)} mg</p>
                </div>
         </div>
         <!-- <div class="col-4 px-0">
             <div class=" text-white" style="background-color:#3A4E48">
                 <p class="py-0 mb-0 lead text-center">Folate</p>
                 <p class="pb-1 text-center lead">{(selectedfood.folate_total_mcg* factor ).toFixed(0)} mcg</p>
                </div>
         </div>
         <div class="col-4 px-0">
             <div class=" text-white" style="background-color:#4381C1">
                 <p class="py-0 mb-0 lead text-center">Vitamin A</p>
                 <p class="pb-1 text-center lead">{(selectedfood.vit_a_iu* factor ).toFixed(0)} IU</p>
                </div>
         </div>
         <div class="col-4 px-0">
             <div class=" text-white" style="background-color:#FE4A49">
                 <p class="py-0 mb-0 lead text-center"> Carotene</p>
                 <p class="pb-1 text-center lead">{(selectedfood.alpha_carot_mcg* factor ).toFixed(0)} mcg</p>
                </div>
         </div> -->
     
     </div>
     
     
     
     
     <div class="row">
         <p class="text-muted text-center">Source: {selectedfood.fct_source}</p>
     </div>
         </div>
           





    </ModalBody>
    <ModalFooter>
      <button class="btn btn-danger" on:click={()=>{
          toggleM()
      }}>CLOSE</button>
    </ModalFooter>
  </Modal>



















</div>