<script>
	import {term, items, showResults, filtered, list} from './stores.js'
	import Search from "./Search.svelte";
	import Listitems from './Listitems.svelte'
	import IoIosPerson from 'svelte-icons/io/IoIosPerson.svelte'
	import User from './User.svelte'

	////Node Mailer



	import {Offcanvas } from 'sveltestrap';
  let open = false;
  const toggle = () => (open = !open);

  let foodname
  $: notified = false


</script>

<div class="container-fluid search-bar">
	<div class="row  py-3">
			<div class="d-flex justify-content-center">
				<Search></Search>
			</div>
	</div>
</div>


{#if $showResults == true && $filtered.length > 0} 

<div class="main">
	<Listitems></Listitems>
</div>


{:else if $showResults == false}

<div class="main">
	<div class="d-flex justify-content-center">
		<img src="./logo nova.png" class="img" alt="LOGO HERE">
	</div>
	<h4 class="mt-0 lead text-center"><strong>Nova Food Facts</strong></h4>
	<p class="text-center">From Nova Wellness & Dietetics</p>
	
</div>

{:else if $showResults == true && $filtered.length == 0}

<div class="main">
	<h2 class="text-muted text-center">Sorry, Nothing to show.</h2>

	
	<div class="col-md-6 mx-auto px-4">
		
		<hr class="mx-auto w-50">

		<p class="text-center strong"><small>
			Feel free to submit a food item you'd want to be added.
		</small></p>

			<div class="form-group mb-2">
			  <label for="fooditem">Food Item</label>
			  <input type="text" 
			  bind:value={foodname} 
			  class="form-control" id="fooditem" aria-describedby="emailHelp" >
			  <small id="emailHelp"
			  class="form-text text-muted">Enter name of the food item you want to be added.</small>
			</div>
			<div class="form-check mb-2">
			  <input type="checkbox" bind:checked="{notified}" class="form-check-input" id="exampleCheck1">
			  <label class="form-check-label" for="exampleCheck1">Get notified when it's been added.</label>
			</div>
			<button type="submit"
			on:click|preventDefault="{()=>{
				window.open(`mailto:novafoodsearch@gmail.com?subject=Nova Food Search - Add a new food &body= Looking to add ${foodname} to Nova Food Search. ${notified ? 'Please notify me when it\'s added':''}`);
			}}"
			class="btn btn-primary">Submit</button>
	
	</div>
	
</div>

{/if}




<div class="float" style="cursor: pointer;" on:click="{()=>{
	toggle()
}}">
	<div class="my-float icon px-auto py-auto">
		<IoIosPerson></IoIosPerson>
	</div>
</div>




<Offcanvas scroll isOpen={open} style="width: 100vw" {toggle}>
   <User/>
  </Offcanvas>


<style>
	.search-bar{
		z-index: 9;
		position: fixed;
		background: rgba(255, 255, 255, 0.25);
		box-shadow: 0 2px 6px 0 rgba(31, 38, 135, 0.37);
		backdrop-filter: blur(14.5px);
		-webkit-backdrop-filter:  blur(14.5px);
		border-radius: 0px;
		border: 1px solid rga(255, 255, 255, 0.18);
	}
	.main{
		margin-top: 85px;
	}

	:root {
    --toastContainerTop: auto;
    --toastContainerRight: auto;
    --toastContainerBottom: 8rem;
    --toastContainerLeft: calc(50vw - 8rem);
	}

	.float{
	position:fixed;
	width:40px;
	height:40px;
	bottom:20px;
	left:20px;
	background-color:#3185FC;
	color:#FFF;
	border-radius:50px;
	align-content: center;
	text-align:center;
	box-shadow: 2px 2px 3px #999;
}

.icon{
    color: white;
    width: 36px;
    height: 36px;
  }
