<script>
    import { TabContent, TabPane } from 'sveltestrap';
    import IoLogoFacebook from 'svelte-icons/io/IoLogoFacebook.svelte';
    import IoLogoWhatsapp from 'svelte-icons/io/IoLogoWhatsapp.svelte';
    import IoIosMail from 'svelte-icons/io/IoIosMail.svelte';
    import IoIosCall from 'svelte-icons/io/IoIosCall.svelte';  
    import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
    import {loggedIn} from './stores.js'
import { onMount } from 'svelte';


    let email
    let password


const auth = getAuth();

onMount(()=>{
    logIn
})


let logIn = ()=>{
    signInWithEmailAndPassword(auth, email, password)
  .then((userCredential) => {
    // Signed in 
    const user = userCredential.user;
    console.log(user)
    if(user){
		loggedIn.set(true)
	}else{
		loggedIn.set(false)
	}
    // ...
  })
  .catch((error) => {
    const errorCode = error.code;
    const errorMessage = error.message;
    alert(errorCode, errorMessage)
  });

}

  




  </script>
  
 <div class="container">

    <div class="row">
        <div class="col">
            <img src="./logo nova.png" class="mx-auto d-block mt-5" alt="..." width="120px" height="120px">
        </div>
    </div>

     <div class="row">
         <div class="col-md-7 mx-auto mt-4">
            <TabContent pills>
                <TabPane tabId="signin" tab="Sign in" active>
                  
                   <div class="container mt-4">
                       <div class="row">
                           <div class="col-12">
                            <div class="form-group mb-2">
                                <label for="fooditem">Enter your ID</label>
                                <input type="text" class="form-control" id="fooddfitem" bind:value="{email}" >
                               </div>
                    
                                <div class="form-group mb-2">
                                    <label for="fooditem">Enter your Password</label>
                                    <input type="password" class="form-control" bind:value="{password}" id="fooditem" >
                                    </div>
            
                                    <button class="btn btn-primary" on:click="{()=>{
                                        logIn()
                                    }}">
                                        Sign In
                                    </button>
            
                           </div>
                       </div>
                   </div>
            
                     
              
                    
                </TabPane>
                <TabPane tabId="signup" tab="Sign up">
                  
                    <div class="container mt-4">
                        <div class="row">
                            <div class="col-10">
                                <h4 class="text-center">
                                    Contact Nova Wellness and Dietetics via the platforms below to get access.
                                </h4>
                            </div>
                        </div>
                        <div class="row">
                             
                            <div class="col-md-7 mx-auto">

                               


                                <div class="d-flex justify-content-center">
                                    <div class="btn-toolbar mb-3" role="toolbar" aria-label="Toolbar with button groups">
                                    <div class="btn-group mr-2" role="group" aria-label="First group">
                                        <button type="button" class="btn icon"
                                        on:click="{()=>{
                                            window.open("https://www.facebook.com/novadietetics.wellness/")
                                        }}"
                                        ><IoLogoFacebook/></button>
                                        <button type="button"
                                        onclick="window.open('tel:+256752667099');"
                                        class="btn icon"><IoLogoWhatsapp/></button>
                                        <button type="button" 
                                        on:click="{()=>{
                                         window.open('mailto:novafoodsearch@gmail.com?subject=Nova Food Search&body=Hello there \n\r I\'d like to sign up for Nova Food Search. Contact me as soon as possible. ');
                                        }}"
                                        class="btn icon"><IoIosMail/></button>
                                        <button type="button" 
                                        onclick="window.open('tel:+256752667099');"
                                        class="btn icon"><IoIosCall/></button>  
                                    </div>
                                    </div>
                            </div>
                        </div>
                    </div>
                    
                </TabPane>
               
              </TabContent>
         </div>
     </div>
 </div>